    <div v-else-if="lang===`en`">
          <h4 class="fw-bold">Banner on the rubble among the clouds</h4>
    <p class="fw-bold">
     Dawn on May 18, 1944, heralded another sunny day. Dream weather. But not around the ruins of the monastery at Monte Cassino. Such aura meant that dozens of Polish soldiers storming the hill would die again in the heat. However, this morning was different. There was silence over the battlefield, broken only from time to time by a distant cannon shot.
    </p>
    <p>
      Such silence was unnatural in this terrible place. The fierce fighting for the hills, protecting the Allies' road to Rome, had been going on for several long months. From January 1944, American, British, French, and New Zealand troops tried to throw select units of German parachute and mountain shooters from their defensive positions. In vain. The Germans decimated the Allied soldiers with precise regularity. The air superiority was useless here. The bombing of the monastery at Monte Cassino only made the situation of the German troops even more favorable - in the ruins it was easier to repel subsequent attacks, and these broke down one after another. Even the most brilliant of the brave, the Gurkhas, could not break through the fire of the "green devils", as the German paratroopers were called. When the soldiers of the 2nd Polish Corps launched an attack on May 12, it looked like the antiquity rule that these cursed hills were impregnable would be confirmed once again. Indeed, the Polish attack broke down, but that was only the first attempt. The Poles had much larger scores to settle with the Germans than the Anglo-Saxons, French, Indians, Canadians, Moroccans, Morys or New Zealanders ... They were bleeding in their won positions, but they kept them and on May 16 they set off again on Specter, San Angelo, Sacrifice Mountain ... The Germans also already they were not as sure as before. They knew their ancient enemy well. And death began to decimate them - Polish death ... Lieutenant Kazimierz Gurbiel thought about all this, absorbing this unnatural silence. But in the end, fatigue took over, and the officer plunged into a nap. Both he and his comrades from the 12th Podolian Lancers Regiment supported the infantry of their Carpathian division. They not only stormed the German bunkers, but also conducted uninterrupted combat patrols as the infantry regrouped for the next attack.
    </p>
    <h4 class="fw-bold">The last patrol</h4>
    <p>
      A familiar voice woke Lieutenant Gurbiel from sleep. It was the commander of his squadron who said that Lieutenant Mieczysław Sander was calling him. Probably the patrol again, the silence hadn't brought anything new, thought Gurbiel, but this time his front gut feeling was inconsistent. It was supposed to be a completely different patrol than the previous ones. When Gurbiel reported to the lieutenant, the lieutenant, without any introduction, immediately found out what it was about: “Kaziu, go to the monastery. Explore what and how. They say the Germans are gone. Don't believe Be careful if they are still sitting there. It turned out that in the morning a white flag was noticed on the ruins of the monastery. Electrified by such an order, Gurbiel did not return for his helmet, sweatshirt and thompson, but as he stood, in his shirt, with a forage on his head and with a colt in a holster, he headed thirteen non-commissioned officers towards the ruins of the monastery. They passed by the stumps of fire-torn olive trees, and they treaded carefully between stones and clumps of red poppies. You had to be careful not only of the decomposing bodies of the fallen in the sun, but also of the mines. At any time, they could also be mowed with a series of spandau or smashed with a mortar shell. They already know the methods of fighting the green devils, who have used the white flag more than once - a sign of surrender, as a ruse to attack unexpectedly. But this time nothing of the sort happened. After several hours of wading along a path between the rocks that the entire Allied 15th Army Group could not conquer, at about ten o'clock the lancers stood in front of the thick and jagged walls of the Benedictine cradle of Christianity. They approached the least dilapidated building, and when Lieutenant Gurbiel pushed open its door, they smelled a terrible stench. There were 16 seriously injured paratroopers inside, with a few paramedics standing next to them. They were tough soldiers, but their eyes betrayed the terror that aroused in them the awareness that Poles would come for them and would take revenge for their crimes in Poland. These, however, did not descend to the level of Hitler's elite. Second Lieutenant Gurbiel said through an interpreter shortly: "Your hair will not fall off your head." Then he went down to the basement under the sanitation room and his hair stood on end under the forage: the casemates were piled with crates with the torn corpses of parachute shooters, and between them lay three more seriously wounded, who were left there to die.
    </p>
    <h4 class="fw-bold">Victory</h4>
    <p>
     Second Lieutenant Gurbiel ran outside to get some fresh air and cool down from the horror that could move the most seasoned veteran. As he was walking among the ruins, he suddenly noticed that two people were climbing up the monastery hill. They turned out to be a Canadian officer and a German prisoner of war as a guide. The Canadian could not hide his disappointment that he was not the one who had won the first place in the enemy's most famous reduct this spring. Behind the Canadian, Uhlan reinforcements reached the summit - the platoon of Lieutenant Leon Hrynkiewicz. One of his lancers, Józef Bruliński, hastily pulled out a pennant of the 12th Podolia Regiment made of a red and navy blue scarf and bandages and hung it high on a pole. And so the amaranth, white and navy blue colors worn by the Podole lancers since Napoleonic times became the first sign announcing the Polish victory at Monte Cassino to the world. It was soon replaced with a white and red flag, next to which, on the orders of General Władysław Anders, the British flag was also hanged. At 11.15 am, second lieutenant Tadeusz Drabczyński released a carrier pigeon, which brought a report to the staff of the 2nd Polish Corps about the occupation of the monastery hill. There was only one capital letter "V" on it - like victory. At noon, the melody of the bugle call played by the platoon Emil Czech played from the ruins of the monastery. This is how Polish soldiers celebrated their key position in Gustaw's line. Although Poles in the surrounding hills were still fighting with the retreating German paratroopers, congratulations fell on General Anders. They emphasized that the steadfastness of his Corps resulted in the victory in the entire Battle of Monte Cassino. Polish soldiers were complimented, giving them the first place among all Allied nations. As predicted by General Anders, Soviet propaganda that Poles do not want to fight the Germans has become a thing of the past. The Polish issue was again on the front pages of newspapers. It seemed that this breakthrough victory, although at a cost of great losses, would finally awaken the conscience of the rulers of the Western world and they would stop Stalin's possessive tendencies towards Poland. Unfortunately, although the 2nd Polish Corps triumphed in Italy more than once, the Americans and the British did not intend to annoy Stalin - for them the potential of the Red Army was more important, without which they could not imagine winning this war. But nothing could erase the memory of the Polish Battle of Monte Cassino - it entered the national pantheon:
    </p>
    <blockquote>
      The doomed fell through the fire,<br>
Many of them got hit and they fell,<br>
Like those madmen of Somosierra,<br>
Like those from Racławice from years ago.<br>
They fell madly<br>
And they came. And the assault went off.<br>
And his white and red banner<br>
They got stuck on the rubble among the clouds.<br>

    </blockquote>

